import { Block, BlockChildren, Data } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

type PlaceholderData = Data;

type PlaceholderChildren = BlockChildren;

export interface PlaceholderProperties<D = PlaceholderData, N extends BlockChildren = PlaceholderChildren> {
  data?: D;
  nodes?: N;
}

export default class Placeholder extends Block<PlaceholderData, PlaceholderChildren> {
  static TYPE = ELEMENT_TYPE;

  static create<SD = PlaceholderData, SN extends BlockChildren = PlaceholderChildren>(
    properties: Placeholder | PlaceholderProperties<SD, SN> | string,
  ) {
    if (typeof properties === 'string') {
      throw new Error('`Placeholder.create` does not accept string');
    }
    if (Placeholder.isPlaceholder(properties)) {
      return properties as Block<SD, SN>;
    }
    return super.create<SD, SN>({
      ...properties,
      type: Placeholder.TYPE,
    });
  }

  static isPlaceholder(maybePlaceholder: any): maybePlaceholder is Placeholder {
    return Block.isBlock(maybePlaceholder) && maybePlaceholder.type === Placeholder.TYPE;
  }
}
